<template>
  <div class="history">
    <el-table
      class="eltable"
      :default-sort="{prop: 'yydate', order: 'descending'}"
      :data="tableData.filter(data => !searchH || data.name.toLowerCase().includes(searchH.toLowerCase()))"
      style="width: 90%"
    >
      <el-table-column label="姓名" prop="name"></el-table-column>
      <el-table-column label="性别" prop="gender"></el-table-column>
      <el-table-column label="预约日期" prop="yydate"></el-table-column>
      <el-table-column label="状态" prop="status"></el-table-column>

      <el-table-column align="right">
        <template slot="header">
          <input class="buginput" type="text" v-model="searchH" placeholder="输入关键字搜索">
        </template>
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">查看</el-button>

          <el-dialog title="信息处理" :visible.sync="dialogFormVisible">
            <el-form :model="form" class="elform">
              <el-form-item label="患者姓名" :label-width="formLabelWidth">
                <el-input class="two" v-model="form.name" autocomplete="off" disabled></el-input>
                <el-popover class="elpopover" placement="left" width="350" trigger="click">
                  <el-table :data="cuslist">
                    <el-table-column width="150" property="yydate" label="日期"></el-table-column>
                    <el-table-column width="100" property="zhenduancontent" label="病情"></el-table-column>
                    <el-table-column width="100" property="keshi" label="科室"></el-table-column>
                  </el-table>
                  <el-button slot="reference" type="primary" @click="getThisCusAllList(form)">查看过往病史</el-button>
                </el-popover>
              </el-form-item>
       
              <el-form-item label="患者病情" :label-width="formLabelWidth">
                <el-input
                disabled
                  v-model="form.content"
                  autocomplete="off"
                  type="textarea"
                  :autosize="{ minRows: 2, maxRows: 4}"
                ></el-input>
              </el-form-item>
              <el-form-item label="开药" :label-width="formLabelWidth">
                 <!-- <el-input class="yaopin" v-model="form.str" autocomplete="off" disabled></el-input> -->
                 <el-input
                 disabled
                  v-model="form.str"
                  autocomplete="off"
                  type="textarea"
                  :autosize="{ minRows: 2, maxRows: 4}"
                ></el-input>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button @click="dialogFormVisible = false">取 消</el-button>
              <el-button type="success" @click="selectSure">确 定</el-button>
            </div>
          </el-dialog>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import {
  getHistoryCuer,
  yaoPinList,
  updateCusStatus,
  getThisCusAllList,
  saveCusDetail,
  removeYyCus
} from "../network/data";
export default {
  props: ["cuser"],
  data() {
    return {
      options: [],
      value: [],
      list: [],
      loading: false,
      states: [],
      dialogTableVisible: false,
      dialogFormVisible: false,
      form: [],
      formLabelWidth: "120px",
      tableData: [],
      searchH: "",
      cuslist: []
    };
  },
  created() {
    this.getHistoryCuer();
    this.yaoPinList();
  },
  methods: {
    getThisCusAllList(form) {
      this.cuslist = [];
      getThisCusAllList(form).then(res => {
        for (let i of res) {
          i.yydate =new Date(i.yydate).toLocaleString()
          this.cuslist.push(i);
        }
        
      });
    },
    selectSure() {
      this.form.medicine = this.value;
      saveCusDetail(this.form).then(res => {
        //把弹窗的信息存进cusdetails
        console.log(res);
      });
      if (this.form.status === "排队中") {
        updateCusStatus(this.form).then(res => {
          this.dialogFormVisible = false;
          this.$router.go("/index/patient");
        });
      } else {
        this.dialogFormVisible = false;
      }
    },
    yaoPinList() {
      yaoPinList().then(res => {
        for (let i = 0; i < res.length; i++) {
          this.states.push(res[i].name);
        }
        this.list = this.states.map(item => {
          return { value: `${item}`, label: `label:${item}` };
        });
      });
    },
    getHistoryCuer() {
      getHistoryCuer(this.cuser).then(res => {
        console.log(res);
        
        this.tableData = [];
        let nowYear, nowMonth, nowDay, nowHours, nowMin, nowSeconds;
        function timeAdd0(str) {
          if (str.length <= 1) str = "0" + str;
          return str;
        }
        for (let i of res) {
          nowYear = new Date(i.yydate).getFullYear().toString();
          nowMonth = (new Date(i.yydate).getMonth() + 1).toString();
          nowDay = new Date(i.yydate).getDate().toString();
          nowHours = new Date(i.yydate).getHours().toString();
          nowMin = new Date(i.yydate).getMinutes().toString(); //获取当前分钟数(0-59)
          nowSeconds = new Date(i.yydate).getSeconds().toString();

          nowYear = timeAdd0(nowYear);
          nowMonth = timeAdd0(nowMonth);
          nowDay = timeAdd0(nowDay);
          nowHours = timeAdd0(nowHours);
          nowMin = timeAdd0(nowMin);
          nowSeconds = timeAdd0(nowSeconds);
          
          if (i.doctorid === this.cuser.doctorid) {
            //遍历所有，找出预约自己的全部患者们
            i.yydate =new Date(i.yydate).toLocaleString()

            if (i.status == "问诊完成") this.tableData.push(i);
          }
        }
      });
    },
    remoteMethod(query) {
      if (query !== "") {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.options = this.list.filter(item => {
            return item.label.toLowerCase().indexOf(query.toLowerCase()) > -1;
          });
        }, 200);
      } else {
        this.options = [];
      }
    },
    handleEdit(index, row) {
      let str=''
      this.form = row;
      if(this.form.medicine.length!=0)
      str=this.form.medicine.join(',')
      this.form.str=str
      this.dialogFormVisible = true;
    },
    handleSuccess(index, row) {
      console.log(index, row);
      if (row.status == "排队中") alert("请诊断");
      else {
        removeYyCus(row).then(res => {
          console.log(res);
          this.$router.go("/index/patient");
        });
      }
    }
  }
};
</script>
<style>
.history{
  width: 80%;
}
.elselct {
  position: absolute;
  left: 0;
}
.el-table {
  position: absolute;
  top: 10%;
  margin-left: 1%;
}
.two {
  position: absolute;
  left: 0;
  width: 30%;
}
.elpopover {
  margin: 0;
  padding: 0;
}
.yaopin{
  width: 500px;
  float: left;
}
.buginput{
  width: 120px;
  height: 20px;
}
</style>